<template>
    <div id="music">
        <h3 class="hd">推荐：{{trackname}}</h3>
        <div class="songs">
            <div class="songhd">
                <span>歌曲</span>
                <span>演唱</span>
                <span>播放</span>
            </div>
            <div class="songsing" v-for="(item,index) in tracks" :key="index">
                <span>{{item.name}}</span>
                <span>{{item.ar[0].name}}</span>
                <router-link class="singa" :to="{path:'/player',query:{id:item.id}}">
                    <span class="iconfont icon-bofang1"></span>
                </router-link>
            </div>
        </div>
        <!-- 2 -->
        <h3 class="hd">精选：{{trackname2}}</h3>
        <div class="songs songlast">
            <div class="songhd">
                <span>歌曲</span>
                <span>演唱</span>
                <span>播放</span>
            </div>
            <div class="songsing" v-for="(item,index) in tracks2" :key="index">
                <span>{{item.name}}</span>
                <span>{{item.ar[0].name}}</span>
                <router-link class="singa" :to="{path:'/player',query:{id:item.id}}">
                    <span class="iconfont icon-bofang1"></span>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            track:{tracks:[]},
            tracks:[],
            trackname:'',
            track2:{tracks:[]},
            tracks2:[],
            trackname2:''
        }
    },
    methods: {
        useraxios(httpUrl){
            return new Promise((resolve,reject)=>{
                 axios.post(httpUrl).then((res)=>{
                    resolve(res.data)
                })
                .catch((res)=>{
                    console.log(res)
                })
            })
            
        },
    },
    beforeMount(){
        this.useraxios(`http://musicapi.leanapp.cn/playlist/detail?id=${this.$route.params.id}`)
        .then((data)=>{
            this.track=data.playlist;
            this.tracks=this.track.tracks;
            this.trackname=this.track.name;      
        });
        this.useraxios('http://musicapi.leanapp.cn/playlist/detail?id=893126993')
        .then((data)=>{
            this.track2=data.playlist;
            this.tracks2=this.track2.tracks;
            this.trackname2=this.track2.name;
        });
    }
    
}
</script>

<style lang="less" scoped>
@mainColor:#00B9F2;
.hd{
    width: 90%;
    margin: 50px auto;
    text-align: left;
    font-size: 24px;
    color: @mainColor;
    cursor: default;
}
.songhd{
    width: 90%;
    height: 30px;
    margin: 0px auto;
    line-height: 30px;
    border-top: 1px solid #E6E6E6;
    border-bottom: 1px solid #E6E6E6;
    background: #F8F9F9;
    display: flex;
    color: @mainColor;
    font-weight: 900;
    font-size: 18px;
    span{
        display: block;
        width: 25%;
        text-align: left;
        font-family: "楷体";
    }
    span:nth-child(1){
        padding-left: 5%;
    }
    span:nth-child(2){
        width: 40%;
        text-align: center;
    }
    span:nth-child(3){
        text-align: right;
    }
}
.songsing{
    .songhd;
    border:none;
    font-weight: normal;
    color: #666;
    cursor: pointer;
    .iconfont{
        display: inline;
        padding-right: 10px;
        color: @mainColor;
    }
}
.songsing span:first-child{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.songsing:nth-child(even){
    background: #FFF;
}
.songsing:hover{
    background: rgba(0,185,242, 0.2);
}
.songlast{
    margin-bottom: 100px;
}
.singa{
    display: block;
    width: 25%;
    text-decoration:none;
    text-align: right;
}
</style>